	}

	.hero-wrapper {
		margin-left: -15px;
		margin-right: -15px;
	}

	.hero-wrapper .hero-title {
		font-size: 48px;
		margin-bottom: 40px;
	}
	
	.hero-wrapper .hero-subtitle {
		font-size: 28px;
		margin-bottom: 40px;
	}

  .hero-wrapper {
    padding-top: 60px;
    margin-top: 0;
    flex-direction: row;
  }

  .hero-wrapper .content {
    text-align: left;
		padding-left: 80px;
  }

  .hero-wrapper .row {
		margin-bottom: 0;
	}

	.center-middle {
		display: flex;
    justify-content: center;
		align-items: center;
		flex-direction: column;
	}

  .main-wrapper .content {
		text-align: left;
		padding-left: 80px;
  }

  .main-wrapper .row {
    min-width: 100%;
    width: 100%;
	}

	.wrapper-visible {
		width: 1200px;
	}

}

